
.contact{
    
    font-family: Arial, Helvetica, sans-serif;
 
}

* {
box-sizing: border-box;

}


input[type=text], select, textarea {
width: 100%;
padding: 12px;
border: 1px solid #ccc;
margin-top: 6px;
margin-bottom: 16px;
resize: vertical;
}

input[type=submit] {
background-color: #04AA6D;
color: white;
padding: 12px 20px;
border: none;
cursor: pointer;
}

input[type=submit]:hover {
background-color: #45a049;
}




.column {
float: left;
width: 40%;
margin-top: 6px;
padding: 20px;
}

.row:after {
content: "";
display: table;
clear: both;
}

@media screen and (max-width: 500px) {
.column, input[type=submit] {
  width: 100%;
  margin-top: 0;
}
}
.con{
border-radius: 5px;
background-color: #f2f2f22a;
padding: 10px;
width: 80%;
margin-left: 5%;
}